#particles-js {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.slider-section .thumbnail {
    margin-bottom: 18px;
    padding: 10px;
    display: inline-block;
}
.gradient-border {
    border-width: 3px;
    background: transparent;
    position: relative;
    border-radius: 50%;
}
.slider-section .thumbnail.gradient-animation img {
    animation: gradient 3s ease infinite;
}
.slider-section .thumbnail.gradient-border img {
    border: none;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    display: flex;
}
.slider-section .thumbnail img {
    border-radius: 50%;
    height: auto;
    width: 320px;
    border: 5px solid #32363b;
}
.gradient-border:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    height: 100%;
    width: 100%;
    background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);
    border-radius: 50%;
    z-index: -1;
    animation: animatedgradient 2s ease alternate infinite;
    background-size: 300% 300%;
    transform: translate(-50%, -50%);
}
@keyframes animatedgradient{
    0%{
        background-position: 0 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0 50%;
    }
}
